@font-face {
  font-family: 'iawriter-mono';
  src: url('../fonts/iAWriterMonoS-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

.container {
  margin: 1rem;
  text-align: center;
}

.nav {
  background-color: #333;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  gap: 2rem;
  padding: 0 1rem;
  height: 7vh;
}

.site-title {
  font-family: 'iawriter-mono', Nitti, Menlo, Courier, monospace;
  font-size: 2rem;
}

.nav ul {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  gap: .5rem;
}

.ul-icon {
  color: inherit;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  transition: color 0.2s;
}

.ul-icon:hover {
  color: #777;
}

.nav a {
  color: inherit;
  text-decoration: none;
  height: 100%;
  display: flex;
  align-items: center;
  padding: .25rem;
}

.nav li {
  display: flex;
  align-items: center;
  border-radius: 5px;
  padding: 5px;
  transition: background-color 0.2s;
}

.nav li.active {
  background-color: #555;
}

.nav li:hover {
  background-color: #777;
}


.patch {
  height: 1.5em;
  /* Makes image match the text size */
  width: auto;
  /* Maintains aspect ratio */
  margin-right: 10px;
  vertical-align: middle;
  /* Aligns with text */
  margin-top: 5px;
  margin-bottom: 5px;
}

.header-tabs{
  justify-content: center;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
}